.Bio {
  position: relative;
  display: block;
  width: 100%;
  padding-top: 150px;
  text-align: center;
  background: rgb(0, 146, 255);
  background: linear-gradient(
    2deg,
    rgba(0, 146, 255, 1) 80%,
    rgba(2, 0, 36, 1) 80%,
    rgba(0, 125, 255, 1) 80%,
    rgba(20, 20, 20, 1) 80%
  );
  font-family: "Source Code Pro", monospace;
  overflow: hidden;
  margin: 0;
}
.Bio::after {
  content: "";
  position: absolute;
  top: 18%;
  left: 0%;
  transform: translateX(50%);
  z-index: 0;
  transform: rotate(2deg);
  height: 30px;
  width: 150%;
  background: rgb(0, 146, 255);
}

.Bio p {
  position: relative;
  z-index: 5;
  margin: 100px 50px 50px 50px;
  color: white;
  display: block;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  padding-bottom: 50px;
}

.Bio p::before {
  line-height: 20px;
  content: '"';
  font-size: 2em;
}

