.Footer {
  position: relative;
  z-index: 6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 50px;
  background: rgb(20, 20, 20);
  color: white;
  -webkit-box-shadow: 0px 10px 83px 6px rgba(20, 20, 20, 1);
  -moz-box-shadow: 0px 10px 83px 6px rgba(20, 20, 20, 1);
  box-shadow: 0px 10px 83px 6px rgba(20, 20, 20, 1);
  font-family: "Source Code Pro", monospace;
}

.Footer ul {
  padding: 0;
}

.Footer ul > li {
  list-style-type: none;
  margin: 0;
  padding-left: 0 !important;
}
