.Projects-Single {
  margin: 50px;
  max-width: 25%;
  text-align: center;
  padding: 0.35rem;
  background-color: #20bf55;
  background: linear-gradient(315deg, #20bf55 0%, rgb(0, 146, 255) 74%);
}

.InnerProjects-Single {
  padding: 2rem;
  background: rgba(20, 20, 20, 1);
}

.Projects-Single h3 {
  font-size: 1.5em;
}

.Projects-Single p > span {
  color: #20bf55;
}

.Projects-Single img {
  width: 80%;
}

.Projects-Single-anchor {
  display: inline-block;
  text-align: center;
  padding: 10px;
  width: 60%;
  background: rgb(0, 146, 255);

  text-decoration: none;
  color: white;
}

@media screen and (max-width: 1024px) {
  .Projects-Single {
    max-width: 45%;
    margin-left: 0;
    margin-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .Projects-Single {
    max-width: 60%;
    margin-left: 0;
    margin-right: 0;
  }
}
@media screen and (max-width: 528px) {
  .Projects-Single {
    max-width: 80%;
    margin-left: 0;
    margin-right: 0;
  }
}
@media screen and (max-width: 360px) {
  .Projects-Single {
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
