.Templates {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Source Code Pro", monospace;
  text-align: center;
  padding: 50px;
  background: rgba(20, 20, 20, 1);
  color: white;
}

.Templates h1:nth-child(2) {
  margin-top: 100px;
  font-size: 4em;

  font-size: 72px;
  background-color: #20bf55;
  background: linear-gradient(315deg, #20bf55 0%, rgb(0, 146, 255) 74%);
  background-clip: text;
  color: transparent;
}

.Templates button {
  display: inline-block;
  font-family: "Source Code Pro", monospace;
  text-align: center;
  padding: 10px;
  width: 150px;
  background: rgb(0, 146, 255);
  border: 0;
  outline: 0;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .Templates h1:nth-child(2) {
    font-size: 3rem;
  }
}

@media screen and (max-width: 528px) {
  .Templates h1:nth-child(2) {
    font-size: 2.2rem;
  }
}

@media screen and (max-width: 400px) {
  .Templates h1:nth-child(2) {
    font-size: 1.7rem;
  }
}
